'use strict';

var Cn = require("re-classnames/src/Cn.bs.js");
var Utils = require("../Utils.js");
var ReasonReact = require("reason-react/src/ReasonReact.js");
var ButtonModuleScss = require("./button.module.scss");

var component = ReasonReact.statelessComponent("Button");

function make(_type, className, children) {
  var newrecord = component.slice();
  newrecord[/* render */9] = (function () {
      var classNames = Cn.make(/* :: */[
            ButtonModuleScss.root,
            /* :: */[
              Cn.ifOpt(className),
              /* [] */0
            ]
          ]);
      return ReasonReact.createDomElement("button", {
                  type: _type,
                  className: classNames
                }, children);
    });
  return newrecord;
}

var s = Utils.s;

exports.component = component;
exports.s = s;
exports.make = make;
/* component Not a pure module */



